<template>
  <article>
    <p>
      <slot></slot>
    </p>
    <div class="tools">
      <input type="text" id="reason" v-model="reason" />
      <button class="completed" @click="answerRequest(true)">✔</button>
      <button class="rejected" @click="answerRequest(false)">✘</button>
    </div>
  </article>
</template>

<script>
import axios from "axios";
export default {
  name: "request",
  props: ["requestId", "requestType"],
  data() {
    return {
      reason: "",
    };
  },
  methods: {
    answerRequest(decision) {
      if (!decision) {
        axios.put(
          `${this.$store.state.server}/${this.requestType}/${this.requestId}`,
          {
            reason: this.reason,
          }
        );
      } else if (this.requestType === "charges") {
        axios.put(
          `${this.$store.state.server}/${this.requestType}/${this.requestId}`,
          {
            amount: Number.parseInt(this.reason),
          }
        );
      } else {
        axios.put(
          `${this.$store.state.server}/${this.requestType}/${this.requestId}`
        );
      }
    },
  },
};
</script>

<style lang="scss" scoped>
article {
  background: rgb(228, 228, 228);
  border-radius: 8px;
  padding: 10px;
  p {
    text-align: left;
    white-space: pre-wrap;
    line-height: 1.5em;
  }
  .tools {
    display: flex;
    flex-flow: row wrap;
    .completed {
      background: green;
    }
    .rejected {
      background: red;
    }
    .completed,
    .rejected {
      color: white;
      border-radius: 4px;
      border: none;
      cursor: pointer;
    }
  }
}
</style>
